.App {
  text-align: center;
  height: 100vh;
}

/* common styles for the all shop */

h1, h2, h3, h4, h5, h6, p {
  margin-bottom: 20px;
}

h1 {
  font-size: 36px;
  line-height: 1.2;
}

body {
  padding-top: 90px;
}

.shop__button {
  background-color: #fff;
  border: 1px solid #ccc;
  outline: none;
  padding: 10px 10px;
  cursor: pointer;
  transition: all .3s;

  &:hover {
    background-color: #3d7ddd;
    color: #fff;
  }
}

.shop__send__messages {
  position: fixed; /* Use fixed para centralizar em relação à viewport */
  background-color: #0fa088;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  padding: 20px;
  color: #fff;
  text-align: center;
  align-content: center;
  font-size: 18px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); /* Centraliza o elemento */
  max-width: 240px;
  border-radius: 5px;
  z-index: 1000;
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
  
  &.fade-out {
    opacity: 0;
  }
}

.container {
  padding-inline: 20px;
}





