.product {
  box-sizing: border-box;
  text-align: left;
  /* background: linear-gradient(rgba(255, 255, 255, 0.9) 98%, rgba(244, 244, 244, 0.8) 100%); */

  &__image__container {
    position: relative;
    width: 100%;
    overflow: hidden;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
  }

  &__image__container:hover {
    .product__image {
      opacity: 0;

      &--hover {
        opacity: 1;
      }
    }
  }

  &__image {
    box-sizing: border-box;
    width: 100%;
    object-fit: cover;
    margin: 0 0 0 0;
    display: block; /* Faz a imagem ocupar toda a largura */
    border-radius: 0px;
    transition: opacity 0.3s ease;

    &--hover {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
    }
  }

  &__elements {
    /* padding: 20px 15px; */
    background-color: #fff;
    padding-block: 15px;
  }

  &__title {
    margin: 0;
    font-size: 16px;
  }

  &__description {
    margin: 10px 0;
    font-size: 13px;
  }

  &__price {
    font-size: 13px;
  }

  &__button {
    margin-top: 20px;
    width: 100%;
    border: none;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;

    svg {
      font-size: 18px;
    }
  }
}



