.footer {
  box-sizing: border-box;
  position: sticky;
  min-height: 100px;
  font-size: 14px;
  align-items: center;
  bottom: 0;
  top: calc(100vh + 150px);
  width: 100%;
  background-color: #f3f3f3;
  padding: 20px;
  text-align: center;
  border-top: 1px solid #ccc;
}

.footer p {
  margin: 5px;
}
