.menu {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  background-color: #fff;
  z-index: 1;

  /*
  transition: all 0.3s;
  opacity: 0.5;
  transform: translateX(-100%);
  pointer-events: none;
  */

  &__nav {
    display: flex;
    flex-direction: column;

    &__buttons {
      margin-top: 25px;
    }

    &__button {
      display: block;
      width: 100%;
      border: 0;
      border-top: 1px solid #ddd;
      
      &:hover {
        background-color: #3d7ddd;
      }
    }

    &__button:last-child {
      border-bottom: 1px solid #ddd;  
    }
  }
}

body.menu__open {
  overflow: hidden;
}
