.contact {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  /*
  opções de grid-template-columns dependendo do layout desejado:

  - grid-template-columns: 2fr 1fr 1fr;  // Primeira coluna maior, outras duas menores  
  - grid-template-columns: 1fr 2fr 1fr;  // Coluna do meio maior, laterais menores
  - grid-template-columns: 1fr 1fr 2fr;  // Última coluna maior, outras duas menores
  - grid-template-columns: 1fr 1fr 1fr;  // Todas as colunas iguais
  - 2fr 1fr 1fr : Primeira coluna ocupa 50%, outras duas 25% cada
  - 1fr 2fr 1fr : Coluna do meio ocupa 50%, laterais 25% cada
  - 25% 50% 25% : Usando porcentagens fixas
  - 300px 1fr 300px : Laterais com largura fixa, meio flexível
  */

  grid-gap: 40px;

  &__aside,
  &__content,
  &__form {
    width: 100%;
  }

  &__image {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
    border-radius: 10px;
  }

  &__form {
    &__elements {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }

    &__input,
    &__textarea,
    &__button {
      width: 100%;
      padding: 10px;
      border: 1px solid #ddd;
      border-radius: 4px;
      font-family: inherit;
      font-size: inherit;
      transition: all 0.3s ease;

      &:focus {
        outline: none;
        border-color: #007bff;
        box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
        background-color: #fcfcfc;
      }
    }

    &__textarea {
      min-height: 150px;
      resize: vertical;
    }

    &__button {
      background-color: #007bff;
      color: #fff;
      cursor: pointer;
      transition: all 0.3s ease;

      &:hover {
        background-color: #0056b3; 
      }
    }
  }
}

@media (max-width: 768px) {
  .contact {
    grid-template-columns: 1fr;
  }
}