.home {
  display: flex;
  gap: 40px;
  padding: 0;
  /*
  justify-items: center;
  justify-content: center;
  */

  &__aside {
    position: sticky;
    top: 140px;
    height: fit-content;
    flex: 0 0 320px;
  }

  &__list {
    list-style: none;
    padding: 0;
    margin: 0 0 20px 0;
  }

  &__content {
    flex: 1;
  }

  &__title {
    margin-bottom: 20px;

    &--h3 {
      margin-bottom: 20px;
      color: #2363b6;
    }
  }

  &__image {
    max-width: 100%;
    height: auto;
    display: block;
    margin-bottom: 20px;
    border-radius: 10px;
  }

  p {
    margin-bottom: 20px;
  }

  @media (max-width: 768px) {
    flex-direction: column;

    &__aside {
      position: relative;
      top: 0;
      flex: 1;
    }
  }
}
